            this.picture = url
            console.log('url', url)

            this.vdata.gambar3 = url
            this.$forceUpdate()
          })
        }
      )
    },
    onUpload4 () {
      this.picture = null
      const storageRef = firebase
        .storage()
        .ref(`${this.imageData.name}`)
        .put(this.imageData)
      storageRef.on(
        `state_changed`,
        (snapshot) => {
          this.uploadValue =
